*{    
    margin: 0px;
    padding: 0px;
}

.container{            
    width: 100%;
    height: 100vh;   
    display: flex;     
    flex-direction: row;
    background-color: white;
}

.food{   
    display: flex;         
    flex-direction: column;    
    margin-left: 4%;   
    width: 45%;  
    max-height: 100%;
    align-items: center;    
    justify-content: center;    
}

.search{
    display: flex;    
    height: 20%;    
    margin-bottom: 10px;
    width: 60%;  
    align-items: center;        
}

.grid_food{                    
    height: 100%;
    width: 100%;    
    margin-bottom: 10%;
    overflow: hidden;
    overflow-y: scroll;        
    display: grid;
    justify-items: center;
    grid-template-columns: 50% 50%;   
    box-shadow: 0px 0px 30px  #909497 ;   
    border-radius: 10px;
    flex-wrap: wrap;
}

.grid_food img{
   margin: 10px;       
   border-radius: 10px;
   height: 200px;   
   width: 200px;   
   box-shadow: 0px 5px 10px black;   
}

.grid_food h1{
    border: 1px;
    border-style: solid;
    width: 90%;
    margin: 2%;
    box-shadow: 0px 5px 10px black;   

}

.icons{    
    width: 40%;    
    display: flex;
    flex-direction: column;    
    align-items: center;
}


.img_food{    
    width: 100%;    
    height: 40%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

#img-container{
    border-radius: 10px;
    width: 300px;
    height: 300px;    
    display: flex;
    justify-content: center;
    align-items: center;    
}

#img-container-hidden{
    border-radius: 10px;
    width: 300px;
    height: 300px;
    display: flex;
    justify-content: center;
    align-items: center;  
    visibility: hidden;  
}


#icons-icons{    
    display: flex;
    gap: 20px;    
}

#search_valor{
    width: 100%;
    padding-left: 2%;
    height: 30%;
    border-radius: 10px;
}

.img-alimento{
    display: flex;
    align-items: center;
    justify-content: center;
    cursor:pointer;
}


.icon_healty{    
    width: 90%;
    margin-top:5%;
    height: 10%;
    display: flex;   
    flex-direction: column; 
}

.icon_enviroment{    
    width: 90%;
    margin-top:5%;
    height: 10%;
    display: flex;  
    flex-direction: column; 
 
}

.icon_economy{    
    width: 90%;
    margin-top:5%;
    height: 10%;
    display: flex;   
    flex-direction: column; 
}

.icon_culture_society{    
    width: 90%;
    margin-top:5%;
    height: 10%;
    display: flex;   
    flex-direction: column; 
}

.props{
    width: 50%;    
    display: flex;
    flex-direction: column;
    align-items: center;    
}

#sub{
    font-size: medium;
    margin-left: 10px;
}

.head_props{
    display: flex;         
    width: 100%;     
    justify-content: center;   
}

#title{
    font-weight: 700;
}

.head_props #title{        
    word-break: break-all;
    text-align: center;
}

.data_props{       
    width: 100%;    
    height: 75%;        
    display: flex;    
    border-radius: 10px;    
    background-color: rgb( 248, 249, 249 );     
    align-items: center;    
    flex-direction: column;    
    overflow-y: scroll;
}

.property{    
    display: flex;
    flex-direction: column;        
    margin-top: 10px;
    margin-bottom: 10px;
}

Input{
    height: 50px;       
}

#atr-titulo{
    width: 100%;
    height: 30px;    
    font-weight: 600;
}

.atr-editable{    
    width: 100%;
    height: 50px;
    border-radius: 10px;
    text-align: center;
    font-size: large;
}

.preparaciones{    
    width: 90%;
    background-color: rgb(236, 236, 236);     
    height: 10%;
    border-radius: 10px;
    margin-bottom: 2%;
    display: flex;
    flex-direction: row;
}

.tags{    
    width: 90%;
    text-align: center;
    overflow-y: scroll;
    padding: 10px;
}

input:invalid:required {
    color: red;    
    -webkit-text-fill-color: red;        
}

#propsform{
    width: 80%; 
}

.add_tag{
    width: 10%;
    margin-top: 10px;
    text-align: center;        
}

#save{    
    margin: 5px;    
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 80%;   
}